@import  "../../assets/scss/app";

.app {
  height: 100vh;
  background: #000;

  .inner {
    background: grey;
    height: 100vh;

    @include desktop {
      width: 1240px;
      margin-left: auto;
      margin-right: auto;
    }

    .content {
      position: relative;
      height: calc(100vh - 50px);
      //display: flex;

      @include desktop {
        height: calc(100vh - 65px);
      }

      .aside {
        position: absolute;
        top: 0;
        left: 0;
        width: $aside_width_desktop;
        height: 100%;
        background: blue;
      }

      .main {
        background: url(../../assets/jpeg/bgr_inner.jpg), #8d6e63;
        background-blend-mode: overlay;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        overflow: auto;
        height: calc(100vh - 50px);
        padding: 16px;
        box-sizing: border-box;

        @include desktop {
          margin-left: $aside_width_desktop;
          height: calc(100vh - 65px);
          padding: 32px;
        }

        .test {
          height: 200vh;
        }

      }


    }

  }
}