@import  "../../assets/scss/app";

.charactersPage {

  .infoBlock {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;

    .count {
      color: $primary_light;
    }
  }

  .divider {
    background-color: $primary_light;
  }

  .list {
    margin-top: 10px;
    margin-left: 5px;

    .group {
      display: flex;
      margin-top: 10px;

      .groupLabel {
        color: $secondary_main;
        flex: 0 0 30px;
        font-size: 36px;
      }

      .listOfGroup {
        flex-grow: 1;
        margin-left: 20px;
      }

      &_location {
        flex-direction: column;

        .groupLabel {
          font-size: 20px;
        }

        .listOfGroup {
          margin-top: 8px;
        }
      }

    }


  }


}